@charset "UTF-8";
/*-----------------------------------*/
html {
  box-sizing: border-box;
}

*, ::before, ::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
}

/*-----------------------------------*/
.header__title {
  font-family: "Montserrat", sans-serif;
}

/*-----------------------------------*/
.menu__checkbox {
  position: absolute;
  left: -9999px;
}

.menu__toggler {
  cursor: pointer; /* při najetí se kurzor změní na ručičku */
}

.menu__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: silver;
}

.menu__item {
  border-bottom: 1px solid darkgray;
}

.menu__link {
  display: block;
  padding: 15px;
  text-decoration: none;
  color: white;
}

.menu__link:hover,
.menu__link:focus {
  background-color: rgb(153, 152, 152);
}

.menu__checkbox:checked ~ .menu__list {
  display: block;
}

/* --------------------------------*/
@media screen and (min-width: 800px) {
  .menu__toggler {
    display: none;
  }
  .menu__list {
    display: flex !important;
  }
  .menu__item {
    border-bottom: 0;
    border-right: 1px solid darkgrey;
  }
}